
  import '/home/runner/work/GPT-Vis/GPT-Vis/src/ConfigProvider/demos/graph-components-config.tsx?watch=parent';
  export default "import { ConfigProvider, GPTVis } from '@antv/gpt-vis';\nimport React from 'react';\n\nconst content = `\n ~~~vis-chart\n{\n  \"type\": \"mind-map\",\n  \"data\": {\n    \"name\": \"台风形成的因素\",\n    \"children\": [\n      {\n        \"name\": \"气象条件\",\n        \"children\": [\n          { \"name\": \"温暖的海水\" },\n          { \"name\": \"气压分布\" },\n          { \"name\": \"湿度水平\" },\n          { \"name\": \"风的切变\" }\n        ]\n      },\n      {\n        \"name\": \"地理环境\",\n        \"children\": [\n          { \"name\": \"大陆架的形状与深度\" },\n          { \"name\": \"海洋暖流的分布\" },\n          { \"name\": \"热带地区的气候特征\" },\n          { \"name\": \"岛屿的影响\" }\n        ]\n      }\n    ]\n  }\n}\n~~~`;\n\nconst mindmapConfig = {\n  type: 'linear',\n  direction: 'right',\n  behaviors: (behaviors: any[]) => [\n    // console.log(behaviors) 👉 [{ key: 'zoom-canvas', type: 'zoom-canvas' }, { key: 'drag-canvas', type: 'drag-canvas' }]\n    // 默认启用两个交互，缩放画布和拖拽画布。此处移除缩放画布并添加拖拽元素\n    ...behaviors.filter((behavior) => behavior.key !== 'zoom-canvas'),\n    {\n      key: 'drag-element',\n      type: 'drag-element',\n    },\n  ],\n  transforms: (prev: any[]) => [\n    // 默认节点支持折叠展开，此处禁用\n    ...prev.filter((transform) => transform.key !== 'collapse-expand-react-node'),\n    {\n      ...prev.find((transform) => transform.key === 'collapse-expand-react-node'),\n      enable: false,\n    },\n  ],\n};\n\nexport default () => (\n  <ConfigProvider\n    components={{\n      MindMap: mindmapConfig,\n    }}\n  >\n    <GPTVis>{content}</GPTVis>\n  </ConfigProvider>\n);\n";;
  