  export default "import { Bubble, type BubbleProps } from '@ant-design/x';\nimport { ChartType, GPTVisLite, NetworkGraph, withChartCode } from '@antv/gpt-vis';\nimport React from 'react';\n\nconst markdownContent = `\n当然了，以下是为你绘制的一个网络图\n\n\\`\\`\\`vis-chart\n{\n  \"type\": \"network-graph\",\n  \"data\": {\n    \"nodes\": [\n      { \"name\": \"哈利·波特\" },\n      { \"name\": \"赫敏·格兰杰\" },\n      { \"name\": \"罗恩·韦斯莱\" },\n      { \"name\": \"伏地魔\" }\n    ],\n    \"edges\": [\n      { \"source\": \"哈利·波特\", \"target\": \"赫敏·格兰杰\", \"name\": \"朋友\" },\n      { \"source\": \"哈利·波特\", \"target\": \"罗恩·韦斯莱\", \"name\": \"朋友\" },\n      { \"source\": \"哈利·波特\", \"target\": \"伏地魔\", \"name\": \"敌人\" },\n      { \"source\": \"伏地魔\", \"target\": \"哈利·波特\", \"name\": \"试图杀死\" }\n    ]\n  }\n}\n\\`\\`\\`\n`;\n\nconst bgStyle = {\n  display: 'grid',\n  gridGap: '20px 0',\n  background: '#f7f7f7',\n  padding: 20,\n  borderRadius: 8,\n  overflow: 'auto',\n};\n\nconst CodeComponent = withChartCode({\n  components: { [ChartType.NetworkGraph]: NetworkGraph },\n  style: { width: 400 },\n});\n\nconst RenderMarkdown: BubbleProps['messageRender'] = (content) => (\n  <GPTVisLite components={{ code: CodeComponent }}>{content}</GPTVisLite>\n);\n\nexport default () => (\n  <div style={bgStyle}>\n    <Bubble\n      placement=\"end\"\n      content=\"在《哈利波特》系列中，有几个主要人物：哈利·波特、赫敏·格兰杰、罗恩·韦斯莱和伏地魔。哈利·波特是主角，他的两个最好的朋友是赫敏·格兰杰和罗恩·韦斯莱。伏地魔是哈利·波特的主要敌人，曾试图杀死哈利。用网络图可视化\"\n      avatar={{\n        src: 'https://mdn.alipayobjects.com/huamei_qa8qxu/afts/img/A*2Q5LRJ3LFPUAAAAAAAAAAAAADmJ7AQ/fmt.webp',\n      }}\n      styles={{ content: { background: '#ebebeb', maxWidth: '60%' } }}\n    />\n    <Bubble\n      content={markdownContent}\n      messageRender={RenderMarkdown}\n      avatar={{\n        src: 'https://mdn.alipayobjects.com/huamei_je4oko/afts/img/A*6LRBT7rjOkQAAAAAAAAAAAAADsZ-AQ/original',\n      }}\n      variant=\"shadow\"\n      styles={{ content: { background: '#fff' } }}\n    />\n  </div>\n);\n";;
  