  export default "import type { BubbleProps } from '@ant-design/x';\nimport { Bubble } from '@ant-design/x';\nimport { ChartType, GPTVisLite, Liquid, withChartCode } from '@antv/gpt-vis';\nimport React from 'react';\n\nconst markdownContent = `\n当然了，以下是为你绘制的一个水波图\n\n\\`\\`\\`vis-chart\n{\n  \"type\": \"liquid\",\n  \"percent\": 0.65,\n  \"title\": \"Liquid Chart Example\"\n}\n\\`\\`\\`\n`;\n\nconst bgStyle = {\n  display: 'grid',\n  gridGap: '20px 0',\n  padding: 20,\n  background: '#f7f7f7',\n  borderRadius: 8,\n  overflow: 'auto',\n};\n\nconst CodeComponent = withChartCode({\n  components: { [ChartType.Liquid]: Liquid },\n  style: { width: 350 },\n});\nconst RenderMarkdown: BubbleProps['messageRender'] = (content) => (\n  <GPTVisLite components={{ code: CodeComponent }}>{content}</GPTVisLite>\n);\n\nexport default () => (\n  <div style={bgStyle}>\n    <Bubble\n      placement=\"end\"\n      content=\"帮我生成电脑内存使用情况占比图\"\n      avatar={{\n        src: 'https://mdn.alipayobjects.com/huamei_qa8qxu/afts/img/A*2Q5LRJ3LFPUAAAAAAAAAAAAADmJ7AQ/fmt.webp',\n      }}\n      styles={{ content: { background: '#ebebeb' } }}\n    />\n    <Bubble\n      content={markdownContent}\n      messageRender={RenderMarkdown}\n      avatar={{\n        src: 'https://mdn.alipayobjects.com/huamei_je4oko/afts/img/A*6LRBT7rjOkQAAAAAAAAAAAAADsZ-AQ/original',\n      }}\n      variant=\"shadow\"\n      styles={{ content: { background: '#fff' } }}\n    />\n  </div>\n);\n";;
  