  export default "import { Bar } from '@antv/gpt-vis';\nimport { Form, Input, Select } from 'antd';\nimport React, { useState } from 'react';\n\nconst data = [\n  { 城市: '七台河', 销售额: 52827.32 },\n  { 城市: '万县', 销售额: 16921.576 },\n  { 城市: '三亚', 销售额: 22698.396 },\n  { 城市: '三岔子', 销售额: 3262.98 },\n  { 城市: '上海', 销售额: 182450.567 },\n  { 城市: '上虞', 销售额: 10672.872 },\n  { 城市: '东丰', 销售额: 1785.84 },\n  { 城市: '东台', 销售额: 2789.892 },\n  { 城市: '东宁', 销售额: 2706.2 },\n  { 城市: '东村', 销售额: 13692.14 },\n  { 城市: '东海', 销售额: 4508.28 },\n  { 城市: '东胜', 销售额: 12766.068 },\n  { 城市: '东莞', 销售额: 10165.89 },\n  { 城市: '东营', 销售额: 17153.92 },\n  { 城市: '中枢', 销售额: 1050.42 },\n  { 城市: '丰县', 销售额: 10309.516 },\n  { 城市: '丰镇', 销售额: 3507.336 },\n  { 城市: '临水', 销售额: 21443.1 },\n  { 城市: '临江', 销售额: 36496.74 },\n  { 城市: '临汾', 销售额: 26205.48 },\n  { 城市: '临沂', 销售额: 97200.74 },\n  { 城市: '临海', 销售额: 7071.456 },\n  { 城市: '临清', 销售额: 38676.12 },\n  { 城市: '丹东', 销售额: 45447.612 },\n  { 城市: '丹江口', 销售额: 4879.616 },\n  { 城市: '丽水', 销售额: 3983.616 },\n  { 城市: '义乌', 销售额: 34511.624 },\n  { 城市: '义马', 销售额: 1144.024 },\n  { 城市: '乌海', 销售额: 16096.64 },\n  { 城市: '乌达', 销售额: 3474.66 },\n  { 城市: '乐城', 销售额: 3241 },\n  { 城市: '乐山', 销售额: 12561.892 },\n  { 城市: '九台', 销售额: 32535.944 },\n  { 城市: '九江', 销售额: 29890.7 },\n  { 城市: '二道江', 销售额: 4461.24 },\n  { 城市: '云浮', 销售额: 6351.212 },\n  { 城市: '云阳', 销售额: 24699.64 },\n  { 城市: '五常', 销售额: 3771.46 },\n  { 城市: '亳州', 销售额: 16961.14 },\n  { 城市: '仙居', 销售额: 6868.512 },\n  { 城市: '仙桃', 销售额: 16600.164 },\n  { 城市: '仪征', 销售额: 8566.628 },\n  { 城市: '任丘', 销售额: 12106.78 },\n  { 城市: '余下', 销售额: 103.04 },\n  { 城市: '余姚', 销售额: 12621.84 },\n  { 城市: '佛山', 销售额: 7500.388 },\n  { 城市: '佳木斯', 销售额: 63263.34 },\n  { 城市: '依兰', 销售额: 26874.82 },\n  { 城市: '保定', 销售额: 124133.1 },\n  { 城市: '信宜', 销售额: 4771.06 },\n  { 城市: '信阳', 销售额: 38849.412 },\n  { 城市: '兖州', 销售额: 28648.2 },\n  { 城市: '公主岭', 销售额: 21210.756 },\n  { 城市: '六合', 销售额: 5775.98 },\n  { 城市: '兰州', 销售额: 42543.144 },\n];\n\nconst themes = ['default', 'academy', 'dark'] as const;\nexport const PALETTE_1 = ['#8459fc', '#ff89bd', '#1677ff', '#00c2ff', '#ff9a00'];\nexport const PALETTE_2 = ['#1B9E77', '#D95F02', '#7570B3', '#E7298A', '#66A61E'];\nexport const PALETTE_3 = ['#7593ed', '#95e3b0', '#6c7893', '#e7c450', '#7460eb'];\n\nexport default () => {\n  const [theme, setTheme] = useState<'default' | 'academy' | 'dark'>('default');\n  const [backgroundColor, setBackgroundColor] = useState<string>('');\n  const [palette, setPalette] = useState<string[]>([]);\n\n  const onValuesChange = (changedValues: {\n    theme?: 'default' | 'academy' | 'dark';\n    backgroundColor?: string;\n    palette?: string[];\n  }) => {\n    if (changedValues.theme) {\n      setTheme(changedValues.theme);\n      setPalette([]);\n    }\n    if (changedValues.backgroundColor !== undefined)\n      setBackgroundColor(changedValues.backgroundColor);\n    if (changedValues.palette !== undefined) {\n      let newPalette = changedValues.palette;\n      if (typeof newPalette === 'string') {\n        try {\n          newPalette = JSON.parse(newPalette);\n        } catch {\n          newPalette = [];\n        }\n      }\n      setPalette(Array.isArray(newPalette) ? newPalette : []);\n    }\n  };\n\n  return (\n    <div>\n      <Form\n        layout=\"inline\"\n        style={{ marginBottom: 12 }}\n        initialValues={{ theme, backgroundColor, palette }}\n        key={theme}\n        onValuesChange={onValuesChange}\n      >\n        <Form.Item label=\"Theme\" name=\"theme\" style={{ marginBottom: 6 }}>\n          <Select style={{ width: 120 }} options={themes.map((t) => ({ label: t, value: t }))} />\n        </Form.Item>\n        <Form.Item\n          label=\"Background\"\n          name=\"backgroundColor\"\n          rules={[\n            {\n              pattern: /^#([A-Fa-f0-9]{6}|[A-Fa-f0-9]{3})$/,\n              message: '请输入有效的色值编码，例如 #fff 或 #ffffff',\n            },\n          ]}\n          style={{ marginBottom: 6 }}\n        >\n          <Input placeholder=\"#ffffff\" style={{ width: 120 }} />\n        </Form.Item>\n        <Form.Item label=\"Palette\" name=\"palette\" style={{ marginBottom: 6 }}>\n          <Select\n            placeholder=\"选择调色板\"\n            style={{ width: 200 }}\n            options={[\n              {\n                label: `色板1: ${PALETTE_1.join(', ')}`,\n                value: JSON.stringify(PALETTE_1),\n              },\n              {\n                label: `色板2: ${PALETTE_2.join(', ')}`,\n                value: JSON.stringify(PALETTE_2),\n              },\n              {\n                label: `色板3: ${PALETTE_3.join(', ')}`,\n                value: JSON.stringify(PALETTE_3),\n              },\n            ]}\n            allowClear\n          />\n        </Form.Item>\n      </Form>\n      {/* @ts-ignore */}\n      <Bar\n        data={data}\n        xField=\"城市\"\n        yField=\"销售额\"\n        axisXTitle=\"城市\"\n        axisYTitle=\"销售额\"\n        theme={theme}\n        style={{\n          backgroundColor,\n          palette,\n        }}\n      />\n    </div>\n  );\n};\n";;
  