  export default "import { Bubble, type BubbleProps } from '@ant-design/x';\nimport { ChartType, FlowDiagram, GPTVisLite, withChartCode } from '@antv/gpt-vis';\nimport React from 'react';\n\nconst markdownContent = `\n当然了，以下是为你绘制的一个流程图\n\n\\`\\`\\`vis-chart\n{\n  \"type\": \"flow-diagram\",\n  \"data\": {\n    \"nodes\": [\n      { \"name\": \"客户下单\" },\n      { \"name\": \"系统生成订单\" },\n      { \"name\": \"仓库拣货\" },\n      { \"name\": \"仓库打包\" },\n      { \"name\": \"物流配送\" },\n      { \"name\": \"客户收货\" }\n    ],\n    \"edges\": [\n      { \"source\": \"客户下单\", \"target\": \"系统生成订单\" },\n      { \"source\": \"系统生成订单\", \"target\": \"仓库拣货\" },\n      { \"source\": \"仓库拣货\", \"target\": \"仓库打包\" },\n      { \"source\": \"仓库打包\", \"target\": \"物流配送\" },\n      { \"source\": \"物流配送\", \"target\": \"客户收货\" }\n    ]\n  }\n}\n\\`\\`\\`\n`;\n\nconst bgStyle = {\n  display: 'grid',\n  gridGap: '20px 0',\n  background: '#f7f7f7',\n  padding: 20,\n  borderRadius: 8,\n  overflow: 'auto',\n};\n\nconst CodeComponent = withChartCode({\n  components: { [ChartType.FlowDiagram]: FlowDiagram },\n  style: { width: 500 },\n});\n\nconst RenderMarkdown: BubbleProps['messageRender'] = (content) => (\n  <GPTVisLite components={{ code: CodeComponent }}>{content}</GPTVisLite>\n);\n\nexport default () => (\n  <div style={bgStyle}>\n    <Bubble\n      placement=\"end\"\n      content=\"可视化用户网购物流全流程\"\n      avatar={{\n        src: 'https://mdn.alipayobjects.com/huamei_qa8qxu/afts/img/A*2Q5LRJ3LFPUAAAAAAAAAAAAADmJ7AQ/fmt.webp',\n      }}\n      styles={{ content: { background: '#ebebeb' } }}\n    />\n    <Bubble\n      content={markdownContent}\n      messageRender={RenderMarkdown}\n      avatar={{\n        src: 'https://mdn.alipayobjects.com/huamei_je4oko/afts/img/A*6LRBT7rjOkQAAAAAAAAAAAAADsZ-AQ/original',\n      }}\n      variant=\"shadow\"\n      styles={{ content: { background: '#fff' } }}\n    />\n  </div>\n);\n";;
  